.registration_form {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.registration_form input {
  width: 80%;
  height: 2rem;
}

@media (min-width: 40rem) {
  .registration_form input {
    width: 20rem;
    height: 2rem;
  }
}
